存，为什么需要用指针间接访问内存呢？这是因为在很多情况下，是找不到变量的，但是所有程序运行后都会加载到内存上，可以通过一些方法定位到所需要处理的内容在内存上的地址。例如在打游戏的时候，只能运行安装的exe文件启动游戏，加载到内存中畅玩，但是并没有游戏的源代码，因此不能通过变量访问内存。但是可以通过一些方法找到我们需要处理的内容在内存上地址。例如控制游戏中的角色受到一些伤害，血量发生变化（尽量控制不要让其他内容发生变化）。对比受伤前后两块内存区域，找到变化的地方，也就找到了代表血量的内存区。这时候你就可以自己写一个程序，用指针修改找到的内存区域的值，让你的英雄“满血复活”。再举一个例子，很多系统将存储真实密码的变量和存储你输入密码的变量是连续定义的，这样它们的内存区是相邻的。通过改变输入，很快就可以定位输入密码的内存位置，将其相邻内存区域的内容复制过来，就可以通过密码验证了。 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"链接b6"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 从以上案例可以看出，通过指针可以直接访问内存区，造成了指针的强大功能，这是Java，Python等没有指针的编程语言所无法办到的。但是同样指针也是一把双刃剑，对不确定内存区域进行修改，会造成程序的错误，甚至系统的崩溃。因此使用指针一定要特别小心，保证指针一定要指向正确的内存区。只要对内存分布、地址编码有了充分的了解，就可以发挥指针的强大功能。 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u3bc2ffb2"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 对于在线评测的算法试题而言，即使完全不懂指针，也不影响题目求解。例如当需要一个函数返回多个值时，C语言只能通过将参数设定为指针进行解决，其实就是让形参访问实参的内存区；但C++中提供了引用功能，可以同样解决问题，但书写和理解上却得到了极大简化，因此在程序设计时不需要必须使用指针。C语言中用字符数组表示字符串，用指针可以更加灵活地进行访问，但是C++中的string类型对字符串的使用进行了良好的封装，也完全可以不使用指针就可以得到良好的性能。 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u3bb48a52"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 无论如何，指针是C/C++中一个强大的功能。了解指针，就能对计算机的基本存储结构有了深层次的理解，对提高程序的性能会有很大帮助。 ")])]),_c('h3',{attrs:{"id":"spGKQ"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 知识点：T615 ")])]),_c('table',{staticClass:"ne-table",staticStyle:{"width":"100%"},attrs:{"border":"1","id":"mow87"}},[_c('tbody',[_c('tr',{staticStyle:{"height":"33px"}},[_c('td',{attrs:{"width":"58"}},[_c('p',{staticClass:"ne-p",attrs:{"id":"ufd2272ed"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 索引 ")])])]),_c('td',{attrs:{"width":"538"}},[_c('p',{staticClass:"ne-p",staticStyle:{"text-align":"center"},attrs:{"id":"ub1bb98b2"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 要点 ")])])]),_c('td',{attrs:{"width":"70"}},[_c('p',{staticClass:"ne-p",attrs:{"id":"uc8c2c69e"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 正链 ")])])]),_c('td',{attrs:{"wi