可以安全地释放。形参和局部变量的生命周期和函数调用的生命周期一致。一个函数所需要的内存大小在编译时就能知道，如果不考虑编译时优化，每个栈帧的大小等于当前函数的所有形参和局部变量的大小总和，外加一些元数据的大小。并且每个形参和局部变量在栈上的内存地址偏移量也都是固定的，所以只要有个变量名就可以直接获取到这个变量对应的内存地址。实际上，变量名被直接编译成偏移量，编译后的二进制代码不存在变量名。 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u7dae4184"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 堆内存是一种按需分配的动态内存管理机制。申请时才分配，申请多少给多少，而栈内存只要调了函数就直接把所有本次调用可能需要的内存都进行分配。动态内存分配机制能让数据一直存在，直到手工销毁，也能让多个线程共享同一个内存里的对象，任何线程只要拿到这片内存的地址和大小就能访问。但是由于它的动态，导致数据访问必须通过指针或引用。此外，堆内存分配耗时较多，对于有时间要求的程序，尽量减少使用动态内存分配。C++中采用new和delete进行堆内存的动态分配和释放，理论上要求new和delete一定要成对出现。因为堆内存分配必须要手动释放，否则会一直存在。如果只分配不释放，可用内存就会越来越少，造成内存泄露。 ")])]),_c('pre',{staticClass:"ne-codeblock language-cpp",attrs:{"data-language":"cpp","id":"V2xui"}},[_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("代码6.13")]),_vm._v(" 堆内存与动态空间分配"),_c('code',[_vm._v("#include <iostream>\nusing namespace std;\nint main()\n{\n    int *a = new int;                    //动态申请一个int类型空间\n    int *a1 = new int(5);                //动态申请一个int类型的空间并且初始化为5\n    int *a2 = new int[5];                //动态申请5个int类型数组\n    int *a3 = new int[5]{1, 2, 3, 4, 5}; // C++11开始支持动态数组的初始化\n    *a = 7;\n    cout << *a<<' ' << *a1<<' ' << a2[2]<<' ' << a3[2] << endl;\n    cout << a[0] <<' '<< a[1] <<' '<< a1[0] << endl;\n    delete a;\n    delete a1;\n    delete[] a2;\n    delete[] a3;\n    return 0;\n}")])]),_c('table',{staticClass:"ne-table",staticStyle:{"width":"100%"},attrs:{"border":"1","id":"zevgQ"}},[_c('tbody',[_c('tr',{staticStyle:{"height":"33px"}},[_c('td',{attrs:{"width":"375"}},[_c('p',{staticClass:"ne-p",attrs:{"id":"u4abd3c72"}},[_c('strong',[_c('span',{staticClass:"ne-text"},[_vm._v(" 样例输入 ")])])])]),_c('td',{attrs:{"width":"375"}},[_c('p',{staticClass:"ne-p",attrs:{"id":"u807dcdfb"}},[_c('strong',[_c('span',{staticClass:"ne-text"},[_vm._v(" 样例输出 ")])])])])]),_c('tr',{staticStyle:{"height":"33px"}},[_c('td',{attrs:{"width":"375"}},[_c('p',{staticClass:"ne-p",attrs:{"id":"u4a178f8a"}},[_c('span',{staticClass:"ne